  .studding-process {
    position: relative;
    padding-bottom: 60px ;
  }
  .studding-process_description {
    position: relative;
    z-index: 3;
    margin-bottom: 32px;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 1px 2px rgba(92, 46, 145, 0.08),
      10px 10px 40px rgba(92, 46, 145, 0.24);
    @media screen and (min-width: 768px) {
      padding: 40px;
      margin-bottom: 32px;
    }
    @media screen and (min-width: 1200px) {
      padding: 60px;
      margin-bottom: 0;
      margin-right: auto;
      flex-basis: 64%
    }
    & > p {
      color: black;
      font-size: 16px;
      line-height: 1.45;
    
      @media screen and (min-width: 768px) {
        font-size: 18px;
      }
      @media screen and (min-width: 1200px) {
        font-size: 20px;
      }
      &:not(:first-child) {
        margin-top: 20px;
      
        @media screen and (min-width: 768px) {
          margin-top: 30px;
        }
        @media screen and (min-width: 1200px) {
          margin-top: 24px;
        }
      }
    }
   
    & > b {
      color: black;
      font-size: 12px;
      line-height: 1.45;
      font-weight: 700;
      margin-top: 27px;
      display: block;
      @media screen and (min-width: 768px) {
        font-size: 17px;
        margin-top: 40px;
      }
        @media screen and (min-width: 1200px) {
          margin-top: 60px;
    }
  }
  }
