  .studding-process_timeline {
    position: relative;
      box-shadow: 0px 1px 2px rgba(92, 46, 145, 0.08), 10px 10px 40px rgba(92, 46, 145, 0.24);
      padding: 20px;
      border-radius: 12px;
      @media screen and (min-width: 768px) {
          padding: 40px;
          height: 220px;
      }
      @media screen and (min-width: 1200px) {
        flex-basis: 370px;
        height:auto;
      }
  }
